<template>
    <div>
        <div class="viewperfil" v-if="isVisibleView">
            <div class="d-flex justify-content-between px-3">
                <div>
                    <label class="b-24-black" v-html="t('profile.title')"></label>
                </div>
                <div>
                    <button type="button" class="btn-black" @click="toggleVisibilityPerfil(false)"
