                    </button>
                </div>
            </div>
            <form class="py-5">
                <!-- <div class="mb-3 px-3 text-start">
                <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.usuario')"></label>
                <input type="text" disabled class="form-control py-3" placeholder="Marco1946">
            </div> -->
                <div class="mb-3 px-3 text-start">
                    <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.nombre')"></label>
                    <input type="text" disabled class="form-control py-3" :placeholder="name">
                </div>
                <div class="mb-3 px-3 text-start">
                    <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.correo')"></label>
                    <input type="text" disabled class="form-control py-3" :value="email">
                </div>
                <!-- <div class="mb-3 px-3 text-start">
                <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.movil')"></label>
                <input type="text" disabled class="form-control py-3" placeholder="556 4738290">
            </div> -->
                <div class="mb-3 px-3 text-start">
                    <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.plan')"></label>
                    <input type="text" disabled class="form-control py-3" :value="plan">
                </div>
                <!-- <div class="mb-3 px-3 text-start">
                <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.pago')"></label>
                <input type="text" disabled class="form-control py-3" value="Tarjeta: **** **** **** 1956">
            </div> -->
                <div class="mb-3 px-3 text-start">
                    <label for="disabledTextInput" class="form-label b-16-gray"
                        v-html="t('profile.contraseña')"></label>
                    <input type="password" disabled class="form-control py-3" value="password">
                </div>
                <div>
                    <button type="button" class="btn-black px-3" data-bs-toggle="modal" data-bs-target="#modalPassword"
                        v-html="t('profile.cambiarcontra')">
                    </button>
                </div>
            </form>
        </div>
        <div class="editarPerfil" v-if="!isVisibleView">
            <div class="d-flex justify-content-start px-3">
                <div>
                    <button type="button" class="btn-back" @click="toggleVisibilityPerfil(true)">
                        <img src="../assets/back.png" class="m-auto">
                    </button>
                </div>
                <div class="ps-2">
                    <label class="b-24-black" v-html="t('profile.editarperfil')"></label>
                </div>
            </div>
            <form class="py-5">
                <!-- <div class="mb-3 px-3 text-start">
                <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.usuario')"></label>
                <input type="text" class="form-control py-3" placeholder="Marco1946">
            </div> -->
                <div class="mb-3 px-3 text-start">
                    <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.nombre')"></label>
                    <input type="text" class="form-control py-3" :value="name" id="UserName">
                </div>
                <div class="mb-3 px-3 text-start">
                    <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.correo')"></label>
                    <input type="text" class="form-control py-3" :value="email" id="UserEmail">
                </div>
                <!-- <div class="mb-3 px-3 text-start">
                <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.movil')"></label>
                <input type="text" class="form-control py-3" placeholder="556 4738290">
            </div> -->
                <!-- <div class="mb-3 px-3 text-start">
                    <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.plan')"></label>
                    <input type="text" class="form-control py-3" value="$100 MXN mensual">
                </div> -->
                <!-- <div class="mb-3 px-3 text-start">
                <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.pago')">
                </label>
                <input type="text" class="form-control py-3" value="Tarjeta: **** **** **** 1956">
            </div> -->
            </form>
            <div class="d-flex justify-content-between pb-5 px-3">
                <div>
                    <button type="button" class="btn-gray-white" @click="toggleVisibilityPerfil(true)"
                        v-html="t('profile.cancelar')">
                    </button>
                </div>
                <div>
                    <button type="button" class="btn-black" @click="UpdateInfo" v-html="t('profile.guardar')">
                    </button>
                </div>
            </div>
        </div>
        <!-- Modal -->
        <div class="modal modal-password fade right-slide" style="overflow-y: scroll;" data-bs-backdrop="false"
            id="modalPassword" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-body bg-black text-start px-3">
                        <label class="b-24-white pb-4" v-html="t('profile.cambiarcontra')"></label>
                        <!-- <div class="mb-3 text-start">
                            <label for="disabledTextInput" class="form-label b-16-white"
                                v-html="t('profile.nuevacontra')"></label>
                            <input type="password" class="form-control py-3" value="password">
                        </div>
                        <div class="mb-3 text-start">
                            <label for="disabledTextInput" class="form-label b-16-white"
                                v-html="t('profile.confirmarcontra')"></label>
                            <input type="password" class="form-control py-3" value="password">
                        </div> -->
                        <div class="mb-3 text-start">
                            <label for="disabledTextInput" class="form-label b-16-white"
                                v-html="t('profile.nuevacontra')"></label>
                            <input minlength="8" @input="validatePassword" type="password" v-model="password"
                                class="form-control py-3" id="pass">
                            <div v-if="password && !isValid" class="text-danger mt-2 small">
                                ⚠️ La contraseña debe tener al menos 8 caracteres, una mayúscula, una minúscula, un
                                número y un símbolo.
                            </div>

                            <div v-if="isValid" class="text-success mt-2 small">
                                ✅ Contraseña segura
                            </div>
                        </div>
                        <div class="mb-3 text-start">
                            <label for="disabledTextInput" class="form-label b-16-white"
                                v-html="t('profile.confirmarcontra')"></label>
                            <input minlength="8" @input="validateConfirm" v-model="confirmPassword" type="password"
                                class="form-control py-3" id="confirmPassword">
                            <div v-if="!passwordsMatch && isValid" class="text-danger small mt-1">
                                ❌ Las contraseñas no coinciden
                            </div>
                            <div v-if="passwordsMatch && isValid" class="text-success small mt-1">
                                ✅ Las contraseñas coinciden
                            </div>

                        </div>
                        <div class="mb-3 form-label b-16-white text-start">
                            <input class="mt-3 " type="checkbox" @click="showPassword()"> {{ t('account.showhide') }}
                        </div>
                        <div class="d-flex justify-content-between">
                            <div>
                                <button type="button" class="btn-gray" data-bs-dismiss="modal"
                                    v-html="t('profile.cancelar')">
                                </button>
                            </div>
                            <div>
                                <button type="button" class="btn-white" @click="SetNewPass()"
                                    v-html="t('profile.guardar')">
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!-- Modal -->
    <div class="modal fade" id="modalFavorite" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-body px-3">
                    <div class="p-4 d-flex justify-content-center">
                        <label class="form-label r-24-gray">{{ message }}</label>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import { useI18n } from 'vue-i18n'
import { API_BASE_URL } from '@/config/api'
const { t } = useI18n()
import { ref, onMounted } from 'vue';
import * as bootstrap from 'bootstrap'
const name = ref("");
const email = ref("");
const plan = ref("");
const message = ref("")
const isVisibleView = ref(true);
const toggleVisibilityPerfil = (val) => {
    isVisibleView.value = val;
};
const isFavoriteQuestion = ref(false)

onMounted(() => {
    GetInfo()
})


async function GetInfo() {
    try {
        const sesion = localStorage.getItem("token")
        if (!sesion) return

        const response = await fetch(`${API_BASE_URL}/api/usuarios/GetUserInfo`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ username: sesion })
        })

        if (!response.ok) throw new Error("Error en la petición")
        else {
            const data = await response.json()

            if (data.data != "") {
                const user = JSON.parse(data.data) ? JSON.parse(data.data) : data.data

                name.value = user.Nombre;
                email.value = user.Correo;
                plan.value = user.Plan;
            }
        }

    } catch (error) {
        console.error("Error al cargar audios:", error)
    }
}

async function SetNewPass() {
    if (!validateForm()) return;

    console.log("contraseña exitosa")
    isFavoriteQuestion.value = !isFavoriteQuestion.value
    try {
        const sesion = localStorage.getItem("token")
        if (!sesion) return

        const response = await fetch(`${API_BASE_URL}/api/usuarios/SetNewPass`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ password: password.value, username: sesion })
        })

        const data = await response.json()

        // 1. Obtener y cerrar el modal de Password
        const modalPassEl = document.getElementById('modalPassword');
        const modalPassInst = bootstrap.Modal.getOrCreateInstance(modalPassEl);
        modalPassInst.hide();

        setTimeout(() => {
            const modalExitoEl = document.getElementById('modalFavorite');
            const modalExitoInst = bootstrap.Modal.getOrCreateInstance(modalExitoEl);
            modalExitoInst.show();

            // 3. Cierre automático del segundo modal tras 2 segundos
            setTimeout(() => {
                modalExitoInst.hide();
            }, 2000); // 2000ms = 2 segundos visibles
        }, 400);

        if (data.status == 1) {
            message.value = "Contraseña actualizada con éxito";
        } else {
            message.value = "Ocurrio un error"
        }



    } catch (error) {
        console.error("Error al cargar audios:", error)
    }

    // 1. Obtener y cerrar el modal de Password
    // const modalPassEl = document.getElementById('modalPassword');
    // const modalPassInst = bootstrap.Modal.getOrCreateInstance(modalPassEl);
    // modalPassInst.hide();

    // 2. Esperar a que termine la animación de cierre (aprox 300ms)
    // Esto evita que el "backdrop" se quede trabado
    // setTimeout(() => {
    //     const modalExitoEl = document.getElementById('modalFavorite');
    //     const modalExitoInst = bootstrap.Modal.getOrCreateInstance(modalExitoEl);
    //     modalExitoInst.show();

    //     setTimeout(() => {
    //         modalExitoInst.hide();
    //     }, 2000); 
    // }, 400);
}

async function UpdateInfo() {
    try {
        const sesion = localStorage.getItem("token")
        if (!sesion) return

        const response = await fetch(`${API_BASE_URL}/api/usuarios/SetUserInfo`, {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ name: document.getElementById('UserName').value, email: document.getElementById('UserEmail').value, username: sesion })
        })

        if (!response.ok) throw new Error("Error en la petición")

        toggleVisibilityPerfil(true)
        GetInfo()

    } catch (error) {
        console.error("Error al cargar audios:", error)
    }
}
const password = ref('')
const isValid = ref(false)
const passwordsMatch = ref(false)
const confirmPassword = ref('')

const validatePassword = () => {
    const regex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[^A-Za-z0-9]).{8,}$/
    isValid.value = regex.test(password.value)
}
const validateConfirm = () => {
    passwordsMatch.value = password.value == confirmPassword.value
}
/***********************Preparación de datos***********************/
function showPassword() {
    var x = document.getElementById("pass");
    var xx = document.getElementById("confirmPassword");
    if (x.type === "password") {
        x.type = "text";
        xx.type = "text";
    } else {
        x.type = "password";
        xx.type = "password";
    }
}

const validateForm = () => {
    // Validar contraseña segura
    const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[^A-Za-z0-9]).{8,}$/;
    if (!passwordRegex.test(password.value)) {
        return false;
    }

    // Validar confirmación de contraseña
    if (password.value !== confirmPassword.value) {
        return false;
    }

    // Si todo está correcto
    return true;
};
</script>

<style scoped>
a {
    text-decoration: none;
    width: 100%;
    display: flex;
    justify-content: center;
}
</style>